import React from 'react';
import { Link } from 'react-router';
import Mains from './public/Main';
import {Imgicon,UserIcon,UerName,Replays,ReplayForm} from './public/base';
import {Tools} from '../Mytool';
class Massage extends React.Component {
       constructor(props){
         super(props)
         this.state= {
           news:'',
           read:false
         };
         this.checkNews=(news,e)=>{
           var {has_read_messages,hasnot_read_messages} = this.props.state.Message,read=this.refs.read.childNodes;
           for(let i =0; i<read.length;i++){
             this.refs.read.childNodes[i].className = ''
           }
           e.target.className = 'on'
           if(news){
             this.setState({
               news:has_read_messages,
             })
           }else {
             this.setState({
               news:hasnot_read_messages,
             })
           }
         }
       }
  render (){
    var {news} = this.state,{hasnot_read_messages} = this.props.state.Message,data =hasnot_read_messages;
    if(news.length>0){
      data=news
    }

    return(
      <div>
        <section>
          <article>
            <div className="message news">
              <div  className="goBack">
                <div>
                  <i onClick={this.props.router.goBack}  className="iconfont">&#xe633;</i>
                  
                </div>
              </div>
              <div className="content">
                <div style={!data ? {display:"none"} :{display:"block"}} >
                  <div ref="read" className="read">
                    <span className="on" onClick={(e)=>this.checkNews(false,e)}></span>
                    <span onClick={(e)=>this.checkNews(true,e)}></span>
                  </div>
                  <div className="fill"></div>
                </div>

                <ul>
                  {data ? data.map((item,index)=>{
                    let {type, author, topic, reply, has_read,id} = item,content;
                    if (type == 'at') {
                      content = <div><Link to={`/topic/${topic.id}`}>{topic.title}</Link> @</div>;
                    } else {
                      content = <div><Link to={`/topic/${topic.id}`}>{topic.title}</Link></div>
                    }
                    return(
                      <li  key={index}>
                        <div className="user">
                          <Link className="user" to={`/user/${author.loginname}`}><UserIcon state={author} /></Link>
                          <div className="user-info">
                            <p>
                              <UerName state={author} style={{color: "rgb(100, 181, 4)",fontSize:" 1.2em"}}/>
                            </p>
                            <div>{content}</div>
                          </div>
                        </div>

                      </li>
                    )
                  } ) :  <li><div className="to-singIn">  <Link to="/singin"></Link> !</div></li>}
                </ul>
              </div>

            </div>
          </article>
        </section>
      </div>
    )
  }


}



export  default Mains(
  {
  id: 'Message',  //redux
  component: Massage, //
  url: 'messages',
  data:{
    stop:(props,state)=>{
      if(!props.state.actions.singin.accesstoken &&!props.state.actions.logIn.accesstoken){
        return false
      }else{
        return true;
      }
    },
    params:(props,state)=>{
      if(props.state.actions.singin.accesstoken){
        return {accesstoken: props.state.actions.singin.accesstoken}
      }else if(props.state.actions.logIn.accesstoken){
        return {accesstoken:props.state.actions.logIn.accesstoken}
      }
    },
    scrollSend:false,
  }
  ,
  success: (state) => {return state; }, //
  error: (state) => { return state } //
});




