import React from 'react';
import { connect } from 'react-redux';
import { Link } from 'react-router';
import {actions} from '../actions/index';
import {NaveBottom} from './public/base';
import {Tools} from '../Mytool';
class Public extends React.Component {
  constructor(props){
    super(props);
    this.state={
      tab:'',
      title:'',
      content:'',
      accesstoken:'',
      showStyle:false,
    };
    this.show = ()=>{
      var {singin,logIn} = this.props.state.actions;
      if(!singin && !logIn){this.props.router.push({
        pathname: '/singin'
      });}
      this.setState({showStyle:!this.state.showStyle})
    }

    this.send =()=>{

       var {tab,title,content} =this.refs;
        this.state={
          tab:tab.value,
          title:title.value,
          content:content.value,
          accesstoken:this.props.state.actions.logIn.accesstoken || this.props.state.actions.singin.accesstoken,
        };
      this.setState({showStyle:false})
      var {state} = this;

        if(!state.tab){
         return alert("")
        }else if(state.title.length <10){
          return alert('10')
        }else if(state.content.length<30){
          return alert('30')
        }

      Tools.post('topics', state, (data) => {
        if (data.success) {
          this.props.router.push({
            pathname: '/topic/' + data.topic_id
          });
        }
      }, () => {
        alert('');
      });
    };
    this.list=(list,status)=>{
      this.setState({list:list,on:status});
    };
  }
  render (){
    var showStyle=this.state.showStyle,data={bottom:true},
      loginname=()=>{
        if( this.props.state.actions.logIn){
          return this.props.state.actions.logIn
        }
        return this.props.state.actions.singin
      }
      ;
    return(
      <div>
        <section>
          <article>
            <div className="loading" style={showStyle ? {display:'block'} :  {display:'none'}}>
              <div className="confirm">
                <h2></h2>
                <div>
                  <span onClick={()=>this.show()}> <i  className="iconfont cancel"></i> &nbsp;&nbsp;&nbsp;&nbsp;</span>
                  <span onClick={()=>this.send()}> <i className="iconfont sure"></i>&nbsp;</span>
                </div>
              </div>
            </div>
            <div  className="goBack">
              <div>
                <i onClick={this.props.router.goBack}  className="iconfont">&#xe633;</i>
                  <i onClick={()=>this.show()} className="iconfont apply" ></i>
                
              </div>
            </div>
            <div className="fill"></div>
            {loginname() ?
              <div className="public">
                <div className="select-content">
                  <select name="" id="" ref='tab'>
                    <option value=""></option>
                    <option value="share"></option>
                    <option value="ask"></option>
                    <option value="job"></option>
                  </select>
                </div>
                <div><input type="text" placeholder="10" ref="title"/></div>
                <div><textarea name="" id="" cols="30" rows="10" placeholder="30" ref="content"></textarea></div>
                <div className="public-button"> <button type="button" onClick={()=>this.show()}></button></div>
              </div>
              :
               <div className="to-singIn">  <Link to="/singin"></Link> !</div>
            }

            <NaveBottom state={Object.assign({},this.props,data)} />
          </article>
        </section>
      </div>
    )
  }
  componentWillReceiveProps(np){

  }
  componentDidMount(){
    document.getElementById('loading').style.display = 'none';
  }
}

const userName = (state)=>{
  return {state:state}
};
const login=(dispatch)=>{
  return {login:(action,data)=>dispatch(actions(action,data))}
}

export  default connect(userName,login)(Public);



