import React from 'react';
import { Link } from 'react-router';
import Mains from './public/Main';
import {Imgicon} from './public/base';
import {Tools} from '../Mytool';

class Information extends React.Component {
  constructor(props){
    super(props);
    this.animate = ()=>{
      var nodes=this.refs.nav,headM,refs =this.refs,mbut = refs.button.style;
      if(nodes){
        for (let j=0;j <nodes.children.length;j++){
          nodes.children[j].className='';
        }
        for (let arr=1;arr< nodes.children.length;arr++){
          if(nodes.children[arr].children[0].className == this.props.location.query.tab ){

            headM=nodes.children[arr].children[0].offsetLeft + "px";
            nodes.children[arr].className='add';
          }
          if(!this.props.location.search){
            nodes.children[0].className='add';
            headM = 0;
          }
        }
      }
      mbut.left =headM;
    };
  }

  render (){
    var data = this.props.state.Topics;

    return(
      <div>
        <div className="navBg">
          <div className="fixedNav">
            <div className="button" ref="button"></div>
            <ul className="nav" id="nav" ref="nav">
              <li >
                <Link to="/">
                  
                </Link>

              </li>
              <li >
                <Link className="good" to="/?tab=good">
                  
                </Link>
              </li>
              <li >
                <Link className="share" to="/?tab=share">
                   
                </Link>
              </li>
              <li >
                <Link className="ask" to="/?tab=ask">
                   
                </Link>
              </li>
              <li >
                <Link className="job" to="/?tab=job">
                   
                </Link>
              </li>
            </ul>
          </div>
        </div>

        <div className="main">
          <ul id="main" ref='main'>
            {data.map(t=>(
              <li key={t.id}>
                <Link to={"/topic/" + t.id}>
                  <div className="itemList" >
                     <Imgicon state={t} />
                    <span>{t.title}</span>
                  </div>
                  <div className="user">
                    <i className="user-icon"  style={{background:'url(' + t.author.avatar_url + ') center',backgroundSize: 'cover'}}></i>
                    <div className="user-info">
                      <p>  <span className="visit">{t.reply_count} / {t.visit_count}-</span>
                        <span className="user-name">{t.author.loginname}</span>
                      </p>
                      <p>  <span className="reply">{Tools.formatDate(t.create_at)}-</span>
                        <span className="create">{Tools.formatDate(t.last_reply_at)}</span></p>
                    </div>
                  </div>
                </Link>
              </li>
            ))}
          </ul>

        </div>
      </div>
    )
  }

  componentDidMount() {
    this.animate();
    var refs =this.refs,list = refs.nav.childNodes,mbut = refs.button.style,_windown=window,nav=document.getElementById('nav') ,
      ofleft,_this=this,ipathnam=_this.props.location.pathname;
    mbut.height =list[0].clientHeight +'px' ;
    mbut.width =list[0].clientWidth +'px' ;
    _windown.onresize = function(){
      if(ipathnam !=location.pathname){return}
      mbut.height =list[0].clientHeight +'px' ;
      mbut.width =list[0].clientWidth +'px' ;
      if( nav == null){return}
      var left=nav.childNodes;
      left.forEach((value,index)=>{
        if(left[index].className == 'add'){
          ofleft = left[index].offsetLeft + "px";
        }
      });
      mbut.left = ofleft;
    };
  }
  componentWillReceiveProps(){
    this.animate()
  }
}
export  default Mains(
  {
  id: 'Topics',  //redux
  component: Information, //
  url: 'topics',
  data: (props, state) => { //
    return {
      tab: props.location.query.tab || 'all',
      state:state
    }
  },
  success: (state) => {return state; }, //
  error: (state) => { return state } //
});




